@use "../global/mixin" as *;
@use "../foundation/base" as *;
.l-header {
  width: 100%;
  height: 5rem;
  background: rgba(255, 255, 255, 0.7); // 70%白、背景が少し透ける
  backdrop-filter: blur(8px); // ←背景ぼかすとさらにおしゃれ
  -webkit-backdrop-filter: blur(8px); // Safari対応
  box-shadow: 0px 8px 19px 0px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  z-index: 5000;
  @include breakpoint(tablet) {
    height: 6rem;
  }
  @include breakpoint(pc) {
    height: 7.4rem;
    background: rgba(255, 255, 255, 0.5);
  }
  &__title {
    text-align: center;
    font-size: 2.4rem;
    line-height: 1.75;
    @include breakpoint(tablet) {
      font-size: 2.4rem;
    }
    @include breakpoint(pc) {
      font-size: 3rem;
    }
    @include breakpoint(pc-l) {
      font-size: 3.5rem;
    }
  }
}
